import React from 'react';
import './App.css';
import {StartButton} from './components/StartButton.js'
import {Tax} from "./components/Tax";

function App(props) {
    if(props.store.pageNow == 1)
        return (
            <div className="App">
                <StartButton click={props.store.go22

                } ></StartButton>
            </div>
      );
    if(props.store.pageNow == 2)
        return (
            <div className="App">
                <Tax store={props.store}></Tax>
            </div>
        );
}

export default App;