import React from 'react';
import './OnePayment.css'
import {observer} from "../index";

export let OnePayment = (props) => {

    return (
        <div className="OnePayment">
            <div className="checkbox">
                <div onClick={() => {props.checkFunc(props.num - 1); observer()}} style={{background: props.info.background}} className="checkboxQuadro"></div>
                <div className="checkboxMoney">{props.info.sum}</div>
                <div className="checkboxYear">- {props.num} год</div>
            </div>
            <div className="horLine"></div>
        </div>
    );
}