import React from 'react';
import './SmallButton2Page.css';
import {observer} from "../index";

export let SmallButton2Page = (props) => {
    let propMas = props.styleProps.split(';')

    let myOnClick = () => {
        props.click(props.styleProps.split(';')[4]); observer()
    }

    let myOnMouseEnter = () => {
        props.hover2Buttons(props.styleProps.split(';')[4]); observer()
    }

    let myOnMouseLeave = () => {
        props.leave2Buttons(props.styleProps.split(';')[4]);
        observer()
    }

    return (
        <div style={{marginLeft: propMas[0],
            marginRight: propMas[1],
            background: propMas[2],
            color: propMas[3]}}
            onClick={() => {myOnClick()}}
            onMouseEnter={() =>{myOnMouseEnter()}}
            onMouseLeave={() => {myOnMouseLeave()}}
            className="SmallButton2Page">{props.text}</div>
    );
}