export let Store = function(){
    this.pageNow = 1
    let that = this
    this.go22 = function (){
        that.pageNow = 2
        document.querySelector('.App').style.background = 'rgba(0, 0, 0, 0.3)'
    }
    this.go21 = function (){
        this.pageNow = 1;
        document.querySelector('.App').style.background = 'linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56'
        that.Payments = []
    }
    this.twoButtons = {
        first: {
            activity: 'Active',
            background: 'linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56',
            color: 'white'
        },
        second: {
            activity: 'notActive',
            background: '#EEF0F2',
            color: 'black'
        }
    }
    this.click2Buttons = function (number){
        that.twoButtons.first.background = '#EEF0F2'
        that.twoButtons.second.background = '#EEF0F2'
        that.twoButtons.first.color = 'black'
        that.twoButtons.second.color = 'black'
        if(number == 'first'){
            that.twoButtons.first.background = 'linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56'
            that.twoButtons.first.color = 'white'
        } else{
            that.twoButtons.second.background = 'linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56'
            that.twoButtons.second.color = 'white'
        }
    }
    this.hover2Buttons = function (number){
        if(number == 'first'){
            if(that.twoButtons.first.background == '#EEF0F2')
                that.twoButtons.first.background = '#DFE3E6';
        } else{
            if(that.twoButtons.second.background == '#EEF0F2')
                that.twoButtons.second.background = '#DFE3E6';
        }
    }
    this.leave2Buttons = function (number){
        if(number == 'first'){
            if(that.twoButtons.first.background == '#DFE3E6')
                that.twoButtons.first.background = '#EEF0F2';
        } else{
            if(that.twoButtons.second.background == '#DFE3E6')
                that.twoButtons.second.background = '#EEF0F2';
        }
    }
    this.Payments = []
    this.addPayments = function (){
        let ZP = document.querySelector('.inputZPinput').value
        if(ZP != '' && ZP != 0){
            that.Payments.length = 0
            let s = 260000
            while(s > 0){
                let sum = ZP*0.13*12
                if(sum < s) {
                    that.Payments.push({
                        sum: sum,
                        checked: 'no',
                        background: ''
                    })
                } else{
                    that.Payments.push({
                        sum: s,
                        checked: 'no',
                        background: ''
                    })
                }
                s -= sum
            }
            if(document.querySelector('.redElement'))
                document.querySelector('.inputZP').removeChild(document.querySelector('.redElement'))
            console.log(that.Payments)
        } else{
            if(!document.querySelector('.redElement')) {
                let redElement = document.createElement('div')
                redElement.classList.add('redElement')
                redElement.textContent = 'Поле обязательно для заполнения'
                redElement.style.color = '#EA0029'
                redElement.style.fontSize = '10px';
                redElement.style.lineHeight = '12px';
                redElement.style.fontWeight = 'normal';
                document.querySelector('.inputZP').insertBefore(redElement, document.querySelector('.calculateButton'))
            }
        }
    }

    this.checkOneBox = function (number){
        if(that.Payments[number].checked == 'no'){
            that.Payments[number].checked = 'yes'
            that.Payments[number].background = `url(${require('./images/mdi_done.svg')})`
            that.Payments[number].border = '0 solid black'
        } else{
            that.Payments[number].checked = 'no'
            that.Payments[number].background = ''
            that.Payments[number].border = '1px solid black'
        }
    }

    this.finish = function (){
        that.result = []
        let k = 1
        that.Payments.forEach((el) => {
            if(el.checked == 'yes')
                that.result.push({
                    sum: el.sum,
                    numYear: k
                })
            k += 1
        })
        that.Payments = []
    }
}

export let store = new Store()